.Header {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    width: 100%;
    height: 60px;
    background: #1976d2;
    color: #fff;
    box-shadow: 0px 4px 4px 0px #ccc;
    z-index: 999;
    a {
        margin: 0 20px;
        color: #fff; }
    span {
        margin: 0 20px;
        cursor: pointer; } }
.Select {
    margin: 0 20px; }
.SelectCity {
    margin-left: 10px;
    text-decoration: underline; }
.SelectCity:hover {
    color: #ccc;
    cursor: pointer; }
