.CheckBox {
    display: flex;
    span {
        margin-left: 10px;
        color: #5a5a5a; }
    input {
        width: 0px;
        height: 0px;
        cursor: pointer;
        margin: 0px;
        opacity: 1; }
    div {
        position: relative;
        span {
            top: 4px;
            left: -6px;
            width: 12px;
            height: 12px;
            border-radius: 3px;
            background-color: #535ce4;
            position: absolute; } } }
.Focus {
    animation: border 0.3s linear;
    box-shadow: rgb(111, 255, 176) 0 0 2px 2px;
    border-color: rgb(111, 255, 176); }

@keyframes border {
    0% {
        box-shadow: rgba(111, 255, 176, 0) 0 0 2px 2px;
        border-color: #ccc; } }
