.MainPage {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
.Description {
    margin: 24px 24px;
    p {
        text-align: justify;
        margin-top: 30px; } }
.Scheme {
    display: flex;
    justify-content: center;
    span {
        display: block;
        font-weight: bold;
        padding-bottom: 10px; } }
.Title {
    display: block;
    font-size: 2rem; }

.Card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 300px;
    height: 300px;
    border: 1px solid;
    border-radius: 50px;
    text-align: center;
    padding: 24px;
    margin: 24px; }
.Links {
    margin: 60px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    button {
        padding: 16px;
        width: 250px;
        font-size: 1.8rem; } }

