.Card {
    position: relative;
    background: #fff;
    border: 1px #ccc solid;
    border-radius: 8px;
    width: auto;
    min-width: 250px;
    max-width: 350px;
    margin: 10px;
    overflow: hidden;
    box-shadow: 0px 1px 2px 0px #888888;
    a {
        background-color: rgba(19, 19, 19, 0.15);
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0; }
    a:hover {
        opacity: 0.6; }
    a:active {
        opacity: 1; } }
.ImgContainer {
    height: 200px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
.TextContainer {
    padding: 15px 10px; }
.Price {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 10px; }
