.Button {
    background: transparent;
    outline: none;
    cursor: pointer;
    border: 2px solid #535ce4;
    border-radius: 20px;
    font-weight: 700;
    font-size: 0.9rem;
    padding: 5px 25px;
    color: rgb(68, 68, 68);
    transition: all 0.3s ease-out;
    &:hover {
        box-shadow: #535ce4 0 0 0px 2px; }
    &:focus {
        box-shadow: rgb(111, 255, 176) 0 0 2px 2px;
        border-color: rgb(111, 255, 176); } }
.Primary {
    background: #535ce4;
    color: rgb(255, 255, 255); }
.Active {
    box-shadow: rgb(111, 255, 176) 0 0 2px 2px;
    border-color: rgb(111, 255, 176);
    &:hover {
        box-shadow: rgb(111, 255, 176) 0 0 2px 2px; } }
