.FormField {
    display: flex;
    flex-direction: column;
    span {
        font-size: 0.9rem;
        margin-bottom: 5px;
        margin-left: 10px;
        color: #000; }
    input {
        box-shadow: none;
        border: none;
        border-radius: 0px;
        border-bottom: #ccc solid 2px;
        animation: unborder 0.3s linear;
        &:focus {
            animation: border 0.3s linear;
            box-shadow: none;
            border-bottom-color: rgb(111, 255, 176); } } }

@keyframes border {
    0% {
        border-bottom-color: #ccc; } }

@keyframes unborder {
    0% {
        border-bottom-color: rgb(111, 255, 176); } }
